import React, { Component } from 'react';
import Header from 'Header/Header';
import Navigation from 'Navigation/Navigation';

class App extends Component {
  render() {
    return (
      <div className="app">
        <div className="content__container">
          <Header />
        </div>
        <Navigation />
      </div>
    );
  }
}

export default App;



